import { Component } from '@angular/core';
import {NgbModal, ModalDismissReasons} from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  closeResult: string;
  startTime: Date;
  endTime: Date;
  percentage = 0;
  duration;

  regex: RegExp = /^[\+\-]?P(?!$)(\d+Y)?(\d+M)?(\d+W)?(\d+D)?(T(?=\d+[HMS])(\d+H)?(\d+M)?(\d+S)?)?$/;

  private duration_hours    = 0;
  private duration_minutes  = 0;
  private duration_seconds  = 0;

  getFormattedTimeString(timeToFormat) {
    function msToTime(time) {
      // Pad to 2 or 3 digits, default is 2
      function pad(n, z = 2) {
        return ('00' + n).slice(-z);
      }
      const time2 = time;
      const ms = time % 1000;
      time = (time - ms) / 1000;
      const secs = time % 60;
      time = (time - secs) / 60;
      const mins = time % 60;
      time = (time - mins) / 60;
      const hrs = time % 24;
      time = (time - hrs) / 24;
      const days = time;
      const timeString = pad(hrs) + 'h ' + pad(mins) + 'm ' + pad(secs) + 's';
      if (days === 0) {
        return timeString;
      }
      return days + 'd ' + timeString;
    }
    return msToTime(timeToFormat);
  }
  now() {
    return new Date().getTime();
  }
  updatePercentage() {
    if (this.startTime != null && this.endTime != null) {
      if (this.endTime.getTime() - this.startTime.getTime() < 0) {
        this.percentage = 0;
        return;
      }
      this.percentage = 100 / (this.endTime.getTime() - this.startTime.getTime()) * (this.now() - this.startTime.getTime());
      if (this.percentage < 0) { this.percentage = 0; }
    } else { return 0; }
  }
  getPercentage() {
    return this.percentage;
  }
  getProgressBarText() {
    if (this.percentage === 0) {
      return '';
    }
    const progressBarText = this.percentage.toFixed(2) + '% (' + this.getFormattedTimeString(this.now() - this.startTime.getTime()) + ')';
    return progressBarText;
  }
  setStartToNow() {
    this.startTime = new Date();
  }
  parseNumber(value): number {
    return value ? parseInt(value, 10) : 0;
  }
  parseDuration() {
    if (!this.duration) {
      return;
    }

    const match = this.regex.exec(this.duration);

    if (!match) {
      console.error('DurationPicker: invalid value: ${this.duration}');
      return;
    }
    this.duration_hours    = this.parseNumber(match[6]);
    this.duration_minutes  = this.parseNumber(match[7]);
    this.duration_seconds  = this.parseNumber(match[8]);
  }
  setDuration() {
    this.parseDuration();
    this.endTime = new Date(this.startTime.getTime() + (this.duration_hours * 60 * 60 * 1000) +
      (this.duration_minutes * 60 * 1000) + (this.duration_seconds * 1000));
  }
  getTimeLeft() {
    return Math.max(0, this.endTime.getTime() - this.now());
  }
  getTimeLeftString() {
    if (this.endTime == null) {
      return '- - -';
    }
    return this.getFormattedTimeString(this.getTimeLeft());
  }


  constructor(private modalService: NgbModal) {}

  open(content) {
    this.modalService.open(content, {ariaLabelledBy: 'modal-legal-notice-header'}).result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
    }, (reason) => {
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    });
  }

  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return  `with: ${reason}`;
    }
  }

  ngOnInit() {
    setInterval(() => {
      this.updatePercentage();
    }, 1000);
  }
}
